.notification_wrap {
  display: grid;
  grid-gap: 0.5rem;

  .n_iteam {
    .inner_ {
      display: grid;
      grid-template-columns: 40px 1fr;
      grid-gap: 0.8rem;
      padding: 1rem;
      border-radius: 15px;
      text-decoration: none;
      color: var(--barnd-text);
      position: relative;
      background-color: var(--color-white);
      overflow: hidden;
      //   &::before {
      //     content: "";
      //     position: absolute;
      //     left: 0;
      //     top: 0;
      //     width: 100%;
      //     height: 100%;
      //     background-color: var(--color-white);
      //   }
    }
    .icon_ {
      position: relative;
      height: 40px;
      border-radius: 50%;
      position: relative;
      z-index: 5;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        width: 17px;
        height: auto;
        max-height: 17px;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: var(--brand-color);
        z-index: -1;
        // opacity: 0.5;
      }
    }
    .c_ {
      display: grid;
      grid-gap: 0.5rem;
    }
    .date_ {
      font-size: 0.8rem;
      font-weight: 300;
      color: var(--brand-text-light);
    }
  }
}
