.med_item {
  .inner_ {
    position: relative;
    display: grid;
    grid-gap: 0.3rem;
    color: var(--color-white);
  }
  .image_box {
    display: flex;
    position: relative;
    padding-top: 65%;
    height: 0;
    border-radius: 15px;
    overflow: hidden;
    background-color: #dfdfdf;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 60%;
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 1) 100%
      );
      opacity: 0.8;
    }
  }
  .title_ {
    font-weight: 300;
    line-height: 1.3;
    margin-top: 0.5rem;
    font-size: 0.95rem;
  }
  .date_ {
    font-size: 0.75rem;
    line-height: 1.1;
    background-color: rgba(#000, 0.5);
    color: var(--brand-color);
    padding: 0.4rem 0.8rem;
    border-radius: 2rem;
  }

  .content_ {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;

    padding: 1rem;
    z-index: 5;
  }
  .c_ {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

body.dark {
  .med_item .inner_ {
    color: var(--color-black);
    .image_box {
      background-color: #22272b;
    }
  }
}
