.profile_nav_wrap {
  padding: 3rem 1.5rem 2rem;
  position: relative;
  background-color: var(--color-white);
  border-radius: 2rem 0 0;
  display: grid;
  grid-gap: 1.5rem;
  height: 100%;
  .p_iteam {
    .inner_ {
      text-decoration: none;
      font-size: 1.02rem;
      font-weight: 400;
      color: var(--brand-text);
    }
    .t_ {
      display: grid;
      grid-template-columns: 35px 1fr;
      grid-gap: 0.8rem;
      align-items: center;
      .icon_ {
        position: relative;
        height: 35px;
        border-radius: 50%;
        position: relative;
        z-index: 5;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        svg {
          width: 14px;
          height: auto;
          max-height: 14px;
        }
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          background-color: var(--brand-color);
          z-index: -1;
          opacity: 0.19;
        }
      }
    }

    .mode_row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      ion-label {
        font-size: 1.02rem;
        font-weight: 400;
        color: var(--brand-text);
        margin: 0;
      }
      // ion-toggle {
      //   padding-left: 0;
      //   padding-right: 0;
      // }
    }
    .v_ {
      ion-item {
        --padding-start: 0;
        --background: transparent;
        --border-color: transparent;
        --min-height: inherit;
        --border-width: 0;
        --full-highlight-height: 0;
        --inset-highlight-height: 0;
      }
      ion-label {
        font-size: 1.02rem;
        font-weight: 400;
        color: var(--brand-text);
        margin: 0;
      }
      ion-select {
        --padding-start: 0.5rem;
        --padding-top: 0px;
        --padding-bottom: 0px;
        --padding-end: 5px;
        font-size: 0.95rem;
        min-width: 40%;
        padding-inline-end: 0;
        width: 100%;
      }
    }
  }
}

// Arabic
[dir="rtl"] {
  .profile_nav_wrap {
    border-radius: 0 2rem 0;
  }
}

.ios {
  .profile_nav_wrap .p_iteam .mode_row ion-toggle {
    height: 20px;
  }
}

body.dark {
  .profile_nav_wrap .p_iteam .t_ .icon_::before {
    opacity: 0.6;
  }
}
