  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../public/assets/fonts/Poppins/Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../public/assets/fonts/Poppins/Poppins-SemiBold.woff")
    format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../public/assets/fonts/Poppins/Poppins-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../public/assets/fonts/Poppins/Poppins-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Sacramento";
  src: url("../public/assets/fonts/sacramento/Sacramento-Regular.woff2")
      format("woff2"),
    url("../public/assets/fonts/sacramento/Sacramento-Regular.woff")
      format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: "Poppins";
}

ion-content {
  font-family: "Poppins";
}

.link_ {
  text-decoration: none;
  color: var(--brand-text-dark);
  font-size: 0.95rem;
  &:hover,
  &:active {
    color: var(--brand-color);
  }
}

.inner_container {
  // padding-left: 15px;
  // padding-right: 15px;
  padding-bottom: 3.5rem;
}
.input_item {
  &.w_icon {
    position: relative;
    input {
      padding-inline-end: 40px;
    }
    .icon_box {
      position: absolute;
      right: 0;
      width: 40px;
      height: 100%;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        width: 20px;
        fill: rgba(#6e6e6e, 0.8);
        stroke: rgba(#6e6e6e, 0.43);
      }
      &:hover,
      &.active {
        svg {
          //  stroke: rgba(#6e6e6e, 0.7);
          fill: rgba(#6e6e6e, 0.8);
        }
      }
    }
  }
}
.input_box {
  display: grid;
  grid-gap: 1.3rem;

  .helpText {
    font-size: 0.75rem;
    margin-top: 0.3rem;
    color: var(--brand-text-lights);
    opacity: 0.7;
    line-height: 0.9;
    padding-inline-start: 1rem;
  }
}

.input_item {
  --padding-start: 0;
  position: relative;
  label {
    z-index: 10;
    position: absolute;
    left: 1rem;
    font-size: 0.85rem;
    top: 0;
    transform: translateY(14px);
    transition: all 0.2s ease-in-out;
    transform-origin: left;
    color: var(--brand-text-dark);
    pointer-events: none;
    &.active {
      transform: translateY(-16px) scale(0.8);
      color: var(--brand-text-light);
    }
    &.normal_ {
      position: relative;
      // left: 0;
      margin-bottom: 0.5rem;
    }
  }
  input {
    background: var(--brand-bg-white);
    height: 3rem;
    padding: 0 1rem;
    border-radius: 10px;
    border: none;
    box-shadow: none;
    outline: none;
    width: 100%;
    font-size: 0.95rem;
  }

  textarea {
    background: var(--brand-bg-white);
    height: 5rem;
    padding: 0 1rem;
    border-radius: 10px;
    border: none;
    box-shadow: none;
    outline: none;
    width: 100%;
    font-size: 0.95rem;
    resize: none;
    padding-top: 0.3rem;
    line-height: 1.4;
  }
  input[type="file"] {
    opacity: 0;
  }
  span {
    &.invalid {
      position: absolute;
      right: 0;
      width: auto;
      padding: 0.3rem 0.5rem;
      background-color: var(--ion-color-danger);
      color: #fff;
      font-size: 10px;
      line-height: 1;
      top: 0;
      border-radius: 5px;
      transform: translateY(-50%);
      z-index: 10;
    }
  }
  .choose_wrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 3rem;
    display: grid;
    grid-template-columns: auto 1fr 30px;
    background: var(--brand-bg-white);
    border-radius: 10px;
    align-items: center;
    grid-gap: 0.5rem;
    padding-inline-start: 1rem;
    .label_ {
      position: relative;
      transform: none;
      left: 0;
    }
    .choose_name {
      color: var(--brand-text-light);
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      font-size: 0.85rem;
    }
    .icon_box {
      position: relative;
      width: auto;
      svg {
        width: 14px;
      }
    }
  }
}
.button_group,
.input_item {
  border: none;
  --ion-background-color: "transparent";
  --min-height: inherit;
}
ion-item {
  &.input_item {
    background: var(--brand-bg-white);
    border-radius: 10px;
    --inner-border-width: 0;
    font-size: 0.95rem;
    --min-height: 3rem;
    --inner-padding-end: 0;
    overflow: visible;
    --border-color: transparent;
    --inset-highlight-height: 0;
    --full-highlight-height: 0;
    --highlight-height: 0;
    --show-full-highlight: 0;
    --border-width: 0;

    &.item-has-placeholder,
    &.item-has-value {
      ion-label.float_ {
        transform: translateY(-16px) scale(0.8);
        color: var(--brand-text-light);
        max-width: calc(100%);
      }
    }

    ion-label.float_ {
      z-index: 10;
      position: absolute;
      left: 1rem;
      font-size: 0.85rem;
      top: 0;
      transform: translateY(14px);
      transition: all 0.2s ease-in-out;
      transform-origin: left;
      color: var(--brand-text-dark);
      margin: 0;
      max-width: calc(100% - 3rem);
      pointer-events: none;
    }
    ion-select {
      max-width: calc(100vw - 2rem);
      min-width: calc(100vw - 2rem);
      --padding-start: 1rem;
      --padding-end: 10px;
    }
  }
}

.button_group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // margin-top: 1rem;
  &.al_end {
    justify-content: flex-end;
  }
  .link_ {
    color: var(--brand-text-light);
  }
  button {
    margin-inline-start: 0.5rem;
  }
}

.overlay_wrap {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(#2c3339, 0.9);
  z-index: 120;
}
.m_top_5 {
  margin-top: 1rem;
}
.m_top_10 {
  margin-top: 1.5rem;
}
.m_top_1 {
  margin-top: 0.4rem;
}
.section-title {
  font-size: 1.45rem;
  line-height: 1;
  font-weight: 600;
  small {
    color: var(--brand-color);
    font-weight: 400;
    font-size: 50%;
  }
}
.section-small-title {
  font-size: 1.2rem;
  line-height: 1;
  font-weight: 500;
}

.text_box {
  &.normal_ {
    p {
      line-height: 1.9;
    }
  }
  &.lg_ {
    p {
      font-size: 5.4vw;
      font-weight: 300;
    }
  }
  &.md_ {
    p {
      font-size: 4.5vw;
      font-weight: 300;
    }
  }
  p {
    font-size: 0.85rem;
    line-height: 1.7;
    font-weight: 400;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    a {
      font-weight: 500;
      color: var(--brand-text);
    }
  }

  h3 {
    font-size: 1.2rem;
    line-height: 1;
    font-weight: 500;
    margin-bottom: 0.3rem;
    &:not(:first-child) {
      margin-top: 2rem;
    }
  }
  h3 ~ p {
    margin-top: 0;
  }
}

.no_padd_bottom {
  padding-bottom: 0 !important;
}

a {
  text-decoration: none;
}

.abs {
  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.pageSection {
  padding: 2.5rem 1rem 0;

  &:not(.padd_normal) {
    padding-inline-end: 0;
  }
  &.padd_normal {
    padding-top: 1rem;
    padding-bottom: 1rem;
    .pTab::after {
      width: 100%;
    }
  }
  &.p_top_3 {
    padding-top: 2.5rem;
  }
}
.sticky {
  position: sticky;
  top: 0;
  background: #eff0f4;
  z-index: 100;
}

.pageBanner {
  position: relative;
  min-height: 45vh;
  display: flex;
  align-items: flex-end;
  .img_box {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, black 100%);
      opacity: 0.8;
    }
  }
  .content_box {
    padding: 1rem;
    position: relative;
    z-index: 5;
    width: 100%;
    color: var(--color-white);
  }
  h1 {
    margin: 0;
    font-size: 10vw;
    line-height: 1.2;
  }
  h3 {
    font-family: "Sacramento";
    font-size: 9vw;
    line-height: 1;
    margin-top: 1rem;
    margin-bottom: 0;
    color: var(--brand-color);
    &.small_ {
      font-size: 7vw;
      line-height: 1.5;
    }
  }

  .date {
    font-size: 0.95rem;
    margin-top: 0.5rem;
    color: var(--brand-color);
  }
}
.inner_container {
  color: var(--brand-text);
  // min-height: 100%;

  &.fit_h {
    display: flex;
    flex-direction: column;
    height: 100%;
    .pageSection {
      flex: 1;
      display: flex;
      flex-direction: column;
    }
    .profile_nav_wrap {
      height: auto;
      align-items: flex-start;
      flex: 1;
    }
  }
}

.more-btn {
  border: none;
  padding: 0.8rem 2rem;
  font-size: 0.95rem;
  font-weight: 400;
  letter-spacing: 1px;
  background-color: var(--brand-color);
  color: var(--brand-text);
  border-radius: 10px;
  display: inline-block;
  &.color-white {
    background-color: var(--color-white);
  }

  &.small_ {
    padding: 0.5rem 1.5rem;
    font-size: 0.9rem;
  }

  &.color-gray {
    background-color: #e6e6e6;
  }
}

ion-popover {
  --background: #eae1ce;
  .sc-ion-select-popover-md-h ion-list.sc-ion-select-popover-md {
    padding-top: 0px;
    padding-bottom: 0px;
  }
  ion-select-popover {
    ion-item {
      --padding-start: 0;
      --inner-padding-end: 0;
      font-size: 0.9rem;
      --border-color: #dbc9a4;
      --background: #eae1ce;
      &:last-child {
        --inner-border-width: 0;
      }
      ion-label {
        padding-inline-start: 1rem;
      }
    }
  }
}

.normalForm {
  .input_item {
    input {
      background-color: var(--color-white);
    }
    textarea {
      height: 8rem;
      background-color: var(--color-white);
    }
    .choose_wrap {
      background-color: var(--color-white);
    }
  }
  ion-item.input_item {
    background: var(--color-white);
  }
}

.filterGrid {
  display: grid;
  grid-template-columns: repeat(2, calc(50% - 0.5rem));
  grid-gap: 1rem;
  // ion-item.input_item ion-select {
  //   max-width: 45%;
  //   min-width: 45%;
  // }
  .input_item {
    &.full_ {
      grid-column: -1/1;

      .inner_row {
        display: grid;
        grid-template-columns: repeat(2, calc(50% - 0.5rem));
        grid-gap: 1rem;
        margin-top: 1rem;
      }
    }
  }
}

.alert-button.sc-ion-alert-ios {
  font-size: 0.9rem;
  color: var(--brand-text);
  font-weight: bold;
}
.alert-radio-label.sc-ion-alert-ios {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
.select-icon {
  background: red;
  //
}

ion-select::part(icon) {
  color: transparent !important;
  background-image: url(../public/assets/images/down_arrow.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.pTab {
  display: flex;
  align-items: center;
  // padding-bottom: 0.5rem;
  //border-bottom: 1px solid var(--brand-border);
  position: relative;
  overflow: auto;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: calc(100% - 1rem);
    height: 1px;
    background-color: var(--brand-border);
  }
  .tab_link {
    padding: 0 0.6rem 0.5rem;
    color: var(--barnd-text);
    opacity: 0.31;
    //  margin-inline-end: 0.5rem;
    position: relative;
    font-size: 1rem;
    font-weight: 500;
    white-space: nowrap;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 0;
      height: 1px;
      background-color: var(--brand-text);
      transition: all 0.3s ease-in-out;
    }
    &:last-child {
      margin-inline-end: 0;
    }
    &.active {
      opacity: 1;
      &::after {
        width: 100%;
      }
    }
  }
}

body .fancybox__thumbs .carousel__slide .fancybox__thumb::after {
  border-width: 3px;
  border-color: var(--brand-color);
}

.no_result {
  height: 30vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: rgba(#2c3339, 0.15);
  border-radius: 15px;
  color: var(--color-white);
  font-size: 5vw;
  text-transform: uppercase;
  letter-spacing: 2px;
}

// Arabic
[dir="rtl"] {
  @font-face {
    font-family: "Droid Arabic Kufi";
    src: url("../public/assets/fonts/droidarabickufi/DroidArabicKufi-Bold.woff2")
        format("woff2"),
      url("../public/assets/fonts/droidarabickufi/DroidArabicKufi-Bold.woff")
        format("woff");
    font-weight: bold;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: "Droid Arabic Kufi";
    src: url("../public/assets/fonts/droidarabickufi/DroidArabicKufi.woff2")
        format("woff2"),
      url("../public/assets/fonts/droidarabickufi/DroidArabicKufi.woff")
        format("woff");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }
  .no_result {
    letter-spacing: 0;
  }

  font-family: "Droid Arabic Kufi", "Poppins";
  ion-content {
    font-family: "Droid Arabic Kufi", "Poppins";
  }
  .input_item {
    label {
      right: 1rem;
      left: auto;
      transform-origin: right;
    }
    &.w_icon {
      .icon_box {
        left: 0;
        right: auto;
      }
    }
    span.invalid {
      left: 0;
      right: auto;
    }
    .choose_wrap .label_ {
      right: 0;
      left: auto;
    }
  }
  .pTab::after {
    right: 0;
    left: auto;
  }
  ion-item.input_item ion-label.float_ {
    right: 1rem;
    left: auto;
    transform-origin: right;
  }
  .pageBanner h3 {
    font-size: 7vw;
  }
}

body.dark {
  ion-select::part(icon) {
    color: transparent !important;
    background-image: url(../public/assets/images/down_arrow_white.svg);
  }
  .no_result {
    background-color: rgba(#fff, 0.1);
    color: var(--color-black);
  }
  .input_item {
    input {
      color: var(--ion-text-color);
    }
    label {
      color: var(--color-white);
      &.active {
        color: var(--color-black);
      }
      &.normal_ {
        color: var(--color-black);
      }
    }

    &.w_icon {
      .icon_box svg {
        fill: var(--ion-text-color);
        stroke: rgba(#2c3339, 0.55);
      }
    }
  }
  .normalForm .input_item {
    label {
      color: var(--color-black);
    }
    input {
      color: var(--color-black);
    }
  }

  .link_ {
    color: var(--color-black);
  }
  .sticky {
    background: var(--ion-background-color);
  }
  .inner_container .content_box {
    color: var(--color-black);
  }
  .sc-ion-alert-md-h {
    --background: #393d40;
    --ion-text-color: #fff;
  }
  .alert-radio-label.sc-ion-alert-md {
    --ion-color-step-850: #c9c9c9;
  }
  .alert-button.sc-ion-alert-ios {
    color: var(--ion-text-color);
  }
  ion-popover ion-select-popover ion-item {
    color: var(--ion-text-color);
    --ion-color-primary: var(--ion-text-color);
  }
  .more-btn.color-gray {
    background-color: var(--ion-text-color);
  }
  .filterGrid {
    ion-item.input_item {
      background: #22272b;
    }
    .input_item {
      label {
        color: var(--color-black);
        &.active {
          color: var(--brand-text-light);
        }
      }
      input {
        background: #22272b;
        color: var(--color-black);
      }
    }
  }
}

.ios {
  body.dark {
    .alert-checkbox-icon.sc-ion-alert-ios {
      background-color: #cfcfcf;
    }
    [aria-checked="true"].sc-ion-alert-ios
      .alert-checkbox-icon.sc-ion-alert-ios {
      background-color: var(--ion-color-primary);
    }
  }
}

.md {
  body.dark {
    .alert-checkbox-label.sc-ion-alert-md {
      color: #c9c9c9;
    }
  }
}
