body {
  --ion-background-color: #eff0f4;
}

.header_wrap {
  position: relative;
  z-index: 100;
  padding: 0.8rem 1.2rem;
  .nav_box {
    display: grid;
    grid-template-columns: 40px calc(100% - 80px - 2rem) 40px;
    grid-gap: 1rem;
    align-items: center;
    position: relative;
  }
  &.menuOpen {
    .logo_box {
      .inner_ {
        left: 0;
        transform: translateX(0%) translateY(-50%);
      }
    }
  }
  .logo_box {
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    .inner_ {
      display: flex;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      transition: all 0.5s ease-in-out;
      img {
        width: 100%;
        height: auto;
        max-width: 40px;
      }
    }
  }

  .av_box {
    .inner_ {
      width: 40px;
      height: 40px;
      display: flex;
      border-radius: 10px;
      overflow: hidden;
      background-color: #b7b7b7;
      box-shadow: 4px 6px 12px #b7b7b7;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .burger_box {
    display: flex;
    justify-content: flex-end;
    z-index: 10;
  }

  .burger_menu {
    width: 30px;
    height: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0;
    background-color: transparent;
    > span {
      width: 100%;
      height: 3px;
      background-color: var(--brand-burger);
      border-radius: 1rem;
    }
    .mid_ {
      width: 75%;
      transition: all 0.3s ease-in-out 0.3s;
    }
    .bot_ {
      width: 50%;
    }

    &.closing {
      .top_ {
        animation: m_top_re 0.6s linear forwards;
      }
      .bot_ {
        animation: m_bottom_re 0.6s linear forwards;
      }
    }

    &.active {
      .mid_ {
        width: 0;
        transition-delay: 0s;
      }
      .top_ {
        animation: m_top 0.6s linear forwards;
      }
      .bot_ {
        animation: m_bottom 0.6s linear forwards;
      }
    }
  }

  .back_btn {
    .link_ {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 40px;
      height: 40px;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        background-color: rgba(#121314, 0.38);
      }
      svg {
        width: 13px;
        height: auto;
        position: relative;
      }
      &:hover,
      &.active,
      &:focus {
        &::before {
          transform: rotate(90deg);
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }

  .page_title {
    text-align: center;
    font-size: 6.5vw;
    line-height: 1;
    font-weight: 500;
    color: var(--brand-text);
  }

  &.floating {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 110;
    .logo_box {
      opacity: 0;
    }
    .burger_menu > span {
      background-color: var(--color-white);
    }
  }
}

@keyframes m_top {
  from {
    width: 100%;
  }

  50% {
    transform: translateY(11px);
    width: 100%;
  }
  to {
    transform: translateY(11px) rotate(-45deg);
    width: 100%;
  }
}

@keyframes m_top_re {
  to {
    width: 100%;
  }

  50% {
    transform: translateY(11px);
    width: 100%;
  }
  from {
    transform: translateY(11px) rotate(-45deg);
    width: 100%;
  }
}

@keyframes m_bottom {
  from {
    width: 50%;
  }
  25% {
    width: 100%;
  }
  50% {
    transform: translateY(-10px);
    width: 100%;
  }
  to {
    transform: translateY(-10px) rotate(45deg);
    width: 100%;
  }
}

@keyframes m_bottom_re {
  to {
    width: 50%;
  }
  75% {
    width: 100%;
  }
  50% {
    transform: translateY(-10px);
    width: 100%;
  }
  from {
    transform: translateY(-10px) rotate(45deg);
    width: 100%;
  }
}
.main_nav_wrap {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--brand-color);
  z-index: 2;
  padding-top: 15vh;
  clip-path: circle(0% at 92% 2rem);
  pointer-events: none;
  transition: all 0.8s cubic-bezier(0.215, 0.61, 0.355, 1);
  &.show {
    clip-path: circle(150% at 92% 2rem);
    pointer-events: auto;
    .nav_list {
      ul {
        li {
          transform: none;
          opacity: 1;
          transition: all 0.8s cubic-bezier(0.215, 0.61, 0.355, 1);
          @for $i from 1 through 10 {
            &:nth-child(#{$i}) {
              transition-delay: #{$i * 0.05s + 0.2s};
            }
          }
        }
      }
    }
  }
  .bg_ {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 75%;
    height: auto;
    pointer-events: none;
    opacity: 0.14;
    display: flex;
    svg {
      width: 100%;
      height: auto;
    }
  }
  .nav_list {
    padding: 0 1rem;
    position: relative;
    z-index: 5;
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: grid;
      grid-gap: 4vh;
      li {
        transform: translateX(3rem) skewX(10deg);
        opacity: 0;
      }
    }
  }
  .link_ {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 0.5rem;
    color: var(--brand-text);
    font-size: 1.1rem;
    font-weight: 500;
    i {
      font-style: normal;
      opacity: 0.27;
    }
  }
}

// Arabic
[dir="rtl"] {
  .main_nav_wrap {
    clip-path: circle(0% at 8% 2rem);
    .nav_list ul li {
      transform: translateX(-3rem) skewX(-10deg);
    }
    &.show {
      clip-path: circle(150% at 92% 2rem);
      .nav_list ul li {
        transform: none;
      }
    }
    .link_ {
      font-weight: 600;
      i {
        font-weight: 500;
      }
    }
  }

  .header_wrap {
    .logo_box .inner_ {
      right: 50%;
      left: auto;
      transform: translateX(50%) translateY(-50%);
    }
    &.menuOpen {
      .logo_box .inner_ {
        left: auto;
        right: 0;
        transform: translateX(0%) translateY(-50%);
      }
    }
    .back_btn .link_ svg {
      transform: scale(-1, 1);
    }
  }
}

body.dark {
  .header_wrap .av_box .inner_ {
    box-shadow: 4px 6px 12px #22272b;
  }

  .main_nav_wrap {
    background-color: #545454;
  }
  .header_wrap.floating .burger_menu > span {
    background-color: #fff;
  }
}
