.mediaGallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0.8rem;
  .gal_item {
    display: flex;
    position: relative;
    padding-top: 70%;
    border-radius: 10px;
    overflow: hidden;
    background-color: #dfdfdf;
    img {
      width: 100%;
      height: auto;
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      object-fit: cover;
    }
  }
}

body.dark {
  .mediaGallery {
    .gal_item {
      background-color: #22272b;
    }
  }
}
