.search_wrap {
  margin: 1rem 1rem 0;
  display: flex;
  .inner_ {
    display: grid;
    grid-template-columns: 30px 1fr 30px;
    grid-gap: 0.5rem;
    background-color: var(--color-white);
    height: 3rem;
    padding: 0 0.5rem;
    border-radius: 15px;
    width: 100%;
  }
  .d_icon,
  .s_icon {
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      width: 15px;
      max-height: 15px;
    }
  }
  .d_icon {
    svg {
      width: 12px;
      max-height: 10px;
    }
  }
  .input_ {
    display: flex;
    align-items: center;
    label {
      color: rgba(#060606, 0.44);
    }
    input {
      height: 3rem;
      border: none;
      outline: none;
      width: 100%;
      &::placeholder {
        /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: rgba(#060606, 0.44);
        opacity: 1; /* Firefox */
      }

      &:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: rgba(#060606, 0.44);
      }

      &::-ms-input-placeholder {
        /* Microsoft Edge */
        color: rgba(#060606, 0.44);
      }
    }
  }
}

body.dark {
  .search_wrap .input_ label {
    color: rgba(#fff, 0.44);
  }
}
