.login_page {
  --ion-background-color: #eff0f4;
  background-color: var(--ion-background-color);
  .login_wrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem;
  }

  .footer_nav {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 1rem;
    border-top: 1px solid var(--brand-border);
    margin-top: 1rem;
    .link_ {
      margin-inline-start: 1rem;
    }
    label {
      font-size: 1rem;
      color: var(--brand-text-light);
    }
  }

  .title_ {
    text-align: center;

    font-size: 1.4rem;
  }
  .top_ {
    margin-bottom: 1.5rem;
  }
  .note_ {
    font-size: 0.8rem;
    //letter-spacing: 1px;
    color: var(--brand-text-light);
    margin-top: 0.7rem;
    p {
      margin: 0;
    }
  }

  .mid_bar {
    display: flex;
    width: 100%;
    margin: 1.5rem 0;
    height: 1px;
    background-color: var(--brand-border);
  }
  .log_box {
    // flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20vh;
    img {
      width: 40%;
      height: auto;
    }
  }
  .form_box {
    padding: 1rem 1.3rem;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .bg_ {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: auto;
    pointer-events: none;
    opacity: 0.11;
    display: flex;
    z-index: -1;

    img {
      width: 100%;
      height: auto;
    }
  }

  .tab_box {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
    .t_item {
      margin: 0 0.8rem;
    }

    .link_ {
      font-size: 1.2rem;
      color: var(--brand-text-light);
      &.active {
        color: var(--brand-text-dark);
      }
    }
  }
}

body.dark {
  .login_page {
    --ion-background-color: #2c3339;
    .tab_box .link_.active {
    }
  }
  .login_page .title_ {
    color: #fff;
  }
}
