.enquire_wrap {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  z-index: 130;
  overflow: hidden;
  pointer-events: none;

  &.menu_open {
    .inner_box {
      transform: translateY(100%);
    }
  }
  &.active {
    pointer-events: auto;
    .inner_box {
      transform: translateY(0);
    }
    .enquire_form_wrap {
      .title_ {
        opacity: 1;
        transform: none;
        transition-delay: 0.2s;
      }
      .input_box {
        > * {
          transform: none;
          opacity: 1;
          @for $i from 1 through 6 {
            &:nth-child(#{$i}) {
              transition-delay: #{$i * 0.15s + 0.2s};
            }
          }
        }
      }
    }
  }
  .inner_box {
    transform: translateY(calc(100% - 38px));
    transition: all 0.8s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  .top_click {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: auto;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 15px;
      background-color: var(--brand-color);
      z-index: -1;
    }
  }
  .bg_ {
    position: relative;

    width: 100%;
    height: 38px;
    z-index: -1;

    overflow: hidden;
    svg {
      width: auto;
      height: 100%;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
    }
  }
  .icon_box {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    svg {
      width: 18px;
      height: auto;
      &.close_icon {
        width: 13px;
      }
    }
  }
}

.enquire_form_wrap {
  background-color: var(--brand-color);
  padding: 1rem 1.5rem 2rem;
  .title_ {
    font-size: 1.6rem;
    font-weight: 400;
    color: #2c3339;
    margin-bottom: 1rem;
    transform: translateX(2rem);
    opacity: 0;
    transition: all 0.6s ease-in-out;
  }
  .input_box {
    > * {
      transform: translateX(2rem);
      opacity: 0;
      transition: all 0.6s ease-in-out 0.2s;
    }
    .input_item {
      textarea,
      input {
        background-color: rgba(#000, 0.06);
      }
    }
  }
}
