.career_wrap {
  display: grid;
  grid-gap: 1rem;
}

.cr_item {
  .inner_ {
    padding: 1rem;
    border-radius: 15px;
    background-color: var(--color-white);
  }
  .title {
    font-weight: 500;
    font-size: 1.1rem;
    line-height: 1.2;
    margin-bottom: 0.5rem;
  }
  .meta_box {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -1rem;
    .mBox {
      margin-inline-end: 1rem;
      margin-bottom: 1rem;
      font-size: 0.9rem;
      padding-inline-start: 1rem;
      position: relative;
      color: var(--brand-text-light);
      &:first-child {
        margin-inline-start: 0;
        padding-inline-start: 0rem;
        &::before {
          display: none;
        }
      }

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 45%;
        transform: translateY(-50%);
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: var(--brand-color);
      }
    }
  }
  .more-btn {
    margin-top: 0.8rem;
  }
}

// Arabic
[dir="rtl"] {
  .cr_item .meta_box .mBox {
    &::before {
      right: 0;
      left: auto;
    }
  }
}
