.videoSection {
  padding-inline-start: 0;
  padding-inline-end: 1rem !important;
}
.playing {
  .videoWrap {
    .overlayBg {
      img {
        clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
      }
      &:after {
        opacity: 0;
      }
      .play_btn svg {
        &.play_ {
          transform: translateY(-130%);
        }
        &.pause_ {
          transform: none;
        }
      }
    }
  }
}

.videoWrap {
  position: relative;

  .video_box {
    position: relative;
    display: flex;
    video {
      width: 100%;
      height: auto;
    }
  }
  .overlayBg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      transition: all 0.8s ease-in-out;
    }
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: #000;
      opacity: 0.3;
      transition: all 0.8s ease-in-out;
    }
    .play_btn {
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 100;
      overflow: hidden;
      position: relative;
      svg {
        width: 20px;
        height: auto;
        fill: #fff;
        transition: all 0.5s ease-in-out;
        &.pause_ {
          position: absolute;
          left: 0;
          bottom: 0;
          transform: translateY(105%);
        }
      }
    }
  }
}

.valueWrap {
  padding-inline-end: 30vw;
  overflow: hidden;
  .swiper,
  .swiper-container {
    overflow: visible;
  }
  .swiper-slide,
  ion-slide {
    height: auto;
  }
}
.value_item {
  height: 100%;
  .inner_ {
    padding: 1.5rem 1rem;
    background-color: var(--color-white);
    border-radius: 0 0 2rem;
    text-align: left;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    height: 100%;
  }
  .img_box {
    margin-bottom: 1rem;
    display: flex;
    img {
      width: 12vw;
      height: 12vw;
      object-fit: contain;
    }
  }
}
.managementWrap {
  padding-inline-end: 15vw;
  overflow: hidden;
  .swiper,
  .swiper-container {
    overflow: visible;
  }
  .swiper-slide,
  ion-slide {
    height: auto;
  }
}

.management_item {
  height: 100%;
  display: flex;
  width: 100%;
  .inner_ {
    display: flex;
    width: 100%;
    height: 100%;
    color: var(--color-white);
  }
  .img_box {
    position: relative;
    overflow: hidden;
    padding-top: 120%;
    border-radius: 15px;
    width: 100%;
    background-color: #dfdfdf;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 60%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, black 100%);
      opacity: 0.8;
    }
  }
  .content_box {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 10;
    padding: 1rem 1.5rem;
    text-align: left;
  }
  .title_ {
    font-size: 5.5vw;
    font-weight: 500;
    line-height: 1.2;
    margin-bottom: 0.5rem;
  }
  .meta_ {
    font-weight: 300;
    font-size: 0.9rem;
  }
}

// Arabic
[dir="rtl"] {
  .value_item .inner_ {
    text-align: right;
    border-radius: 0 0 0 2rem;
  }
}

body.dark {
  .management_item .img_box {
    background-color: #22272b;
  }
}
