.com_p_item {
  .inner_ {
    position: relative;
    color: var(--brand-text);
  }
  .image_box {
    position: relative;
    padding-top: 62%;
    border-radius: 15px;
    overflow: hidden;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, black 100%);
      opacity: 0.5;
    }
  }
  .content_box {
    display: grid;
    grid-template-columns: 1fr 20%;
    align-items: flex-start;
    padding-inline-end: 0.5rem;
  }
  .c_ {
    padding-top: 0.5rem;
  }
  .c_logo {
    position: relative;
    padding-top: 100%;
    transform: translateY(-50%);
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: #2c3339;
      opacity: 0.82;
      border-radius: 10px;
    }
    img {
      position: absolute;
      left: 10%;
      height: 80%;
      width: 80%;
      top: 10%;
      object-fit: contain;
      z-index: 10;
    }
  }
  .title_ {
    font-size: 1.15rem;
    font-weight: 500;
  }
  .location_ {
    color: var(--brand-text-light);
    font-size: 0.9rem;
    margin-top: 0.1rem;
  }
}
