.homeBanner {
  padding: 0 20vw;
  overflow: hidden;
  margin-top: 2rem;
  .swiper,
  .swiper-container {
    overflow: visible;
  }
  .swiper {
    display: flex;
    flex-direction: column-reverse;
  }
  .h_item {
    width: 100%;
    transition: all 0.3s ease-in-out;
    transform-origin: bottom;
    .inner_ {
      display: flex;
      width: 100%;
      flex-direction: column;
      color: var(--brand-text);
      text-decoration: none;
    }
    .img_box {
      position: relative;
      width: 100%;
      height: 0;
      overflow: hidden;
      border-radius: 10px;
      padding-top: 130%;
      margin-bottom: 1rem;
      background-color: #dfdfdf;
      img {
        width: 100%;
        height: auto;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        object-fit: cover;
      }
    }
    .title_ {
      font-size: 1.3rem;
      line-height: 1.2;
      font-weight: 500;
      margin-bottom: 0.3rem;
      transform: translateY(-1rem);
      opacity: 0;
    }
    .dec_ {
      font-size: 0.9rem;
      font-weight: 300;
      color: #7c7f82;
      line-height: 1.2;
      transform: translateY(-1rem);
      opacity: 0;
    }
  }
  .swiper-slide-active {
    .h_item {
      .title_ {
        transform: none;
        opacity: 1;
        transition: all 0.5s ease-in-out;
      }
      .dec_ {
        transform: none;
        opacity: 1;
        transition: all 0.5s ease-in-out 0.1s;
      }
    }
  }

  .swiper-slide-prev {
    .h_item {
      //transform-origin: 92% 82%;
      //transform-origin: bottom right;
      transform: rotate(-5deg) scale(0.9);
    }
  }
  .swiper-slide-next {
    .h_item {
      // transform-origin: 5% 85%;
      // transform-origin: bottom left;
      transform: rotate(5deg) scale(0.9);
    }
  }

  .swiper-pagination {
    position: relative;
    margin-top: 1rem;

    .swiper-pagination-bullet {
      --bullet-background: #cfd0d4;
      opacity: 1;
      border-radius: 2rem;
      width: 6px;
      height: 6px;
      transition: all 0.3s ease-in-out;
      &.swiper-pagination-bullet-active {
        width: 25px;
      }
    }
  }
}

.homeSection {
  padding: 3.5rem 1rem 0;
  padding-inline-end: 0;
}

.offer_wrap {
  padding-inline-end: 40vw;
  overflow: hidden;
  .swiper,
  .swiper-container {
    overflow: visible;
  }

  .offer_item {
    .inner_ {
      display: flex;
    }
    .img_box {
      width: 100%;
      height: auto;
      overflow: hidden;
      border-radius: 10px;
      display: flex;
      background-color: #dfdfdf;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}

.event_wrap {
  padding-inline-end: 20vw;
  overflow: hidden;
  .swiper,
  .swiper-container {
    overflow: visible;
  }

  .event_item {
    .inner_ {
      display: grid;
      align-items: center;
      grid-template-columns: 1fr 30%;
      //grid-gap: 1rem;
      position: relative;
      color: var(--brand-text);
      text-decoration: none;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 90%;
        height: 100%;
        background-color: var(--color-white);
        border-radius: 10px;
        z-index: -1;
      }
    }

    .img_box {
      position: relative;
      padding-top: 100%;
      border-radius: 50%;
      overflow: hidden;
      background-color: #dfdfdf;
      img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .content_box {
      padding: 1.5rem 1rem;
      text-align: left;
    }
    .title_ {
      font-size: 1.1rem;
      line-height: 1.2;
      margin-bottom: 0.5rem;
      font-weight: 500;
    }
  }
}

// Arabic
[dir="rtl"] {
  .homeBanner {
    .swiper-slide-next {
      .h_item {
        transform: rotate(-5deg) scale(0.9);
      }
    }
    .swiper-slide-prev {
      .h_item {
        transform: rotate(5deg) scale(0.9);
      }
    }
  }
  .event_wrap .event_item {
    .inner_ {
      &::before {
        right: 0;
        left: auto;
      }
    }
  }
}

body.dark {
  .offer_wrap .offer_item .img_box,
  .homeBanner .h_item .img_box {
    background-color: #22272b;
  }
  .event_wrap .event_item .img_box {
    background-color: #3a4248;
  }
  .search_wrap .input_ input {
    background-color: var(--color-white);
    color: #fff;
    &::placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #9d9d9d;
      opacity: 1; /* Firefox */
    }

    &:-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: #9d9d9d;
    }

    &::-ms-input-placeholder {
      /* Microsoft Edge */
      color: #9d9d9d;
    }
  }
}
