.more {
  border: none;
  padding: 0.8rem 2rem;
  font-size: 0.95rem;
  line-height: 1;
  font-weight: 400;
  letter-spacing: 1px;
  background-color: var(--brand-color);
  color: var(--brand-text);
  border-radius: 10px;

  &.color-white {
    background-color: var(--color-white);
  }
}

body.dark {
  .more {
    color: var(--ion-text-color);
    &.color-white {
      color: #fff;
    }
  }
}
