.con_iteam {
  .inner_ {
    position: relative;
    display: grid;
    grid-gap: 0.3rem;
    color: var(--color-white);
  }
  .image_box {
    display: flex;
    position: relative;
    padding-top: 65%;
    height: 0;
    border-radius: 15px;
    overflow: hidden;
    background-color: #dfdfdf;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 60%;
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 1) 100%
      );
      opacity: 0.8;
    }
  }
  .title_ {
    font-weight: 500;
    line-height: 1;
    margin-top: 0.6rem;
    font-size: 1.2rem;
  }
  .date_ {
    font-size: 0.75rem;
    line-height: 1.1;
    background-color: rgba(#000, 0.5);
    color: var(--brand-color);
    padding: 0.4rem 0.8rem;
    border-radius: 2rem;
  }

  .content_ {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 25%;
    grid-gap: 1rem;
    align-items: flex-end;
    padding: 1rem;
    z-index: 5;
  }
  .c_ {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .progressBox {
    position: relative;
    display: flex;
    svg {
      transform: rotate(-90deg);
      width: 100%;
      height: auto;
    }
    .progressBar {
      fill: rgba(#000, 0.3);
      stroke-width: 0.9;
      stroke: var(--brand-color);
      stroke-dasharray: 100 100;
      stroke-dashoffset: 100;
      stroke-linecap: round;
    }
    .persBox {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 10;
      font-size: 1.1rem;
      font-weight: 500;
    }
  }
}

body.dark {
  .con_iteam {
    .inner_ {
      color: var(--color-black);
    }
    .image_box {
      background-color: #22272b;
    }
    .progressBox .progressBar {
      fill: rgba(#fff, 0.3);
    }
  }
}
