.pageBanner.autoH {
  flex-direction: column;
  align-items: flex-start;
  .img_box {
    position: relative;
    padding-top: 130%;
  }
  .content_box {
    position: absolute;
    left: 0;
    bottom: 0;
    display: grid;
    grid-template-columns: 1fr 23%;
    align-items: flex-end;
    grid-gap: 0.5rem;
  }
  .locationBox {
    display: grid;
    grid-template-columns: 15px 1fr;
    grid-gap: 0.5rem;
    align-items: center;
    padding: 1rem;
    width: 100%;
    font-size: 0.9rem;
    padding-inline-end: 30%;

    .icon {
      display: flex;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }

  .logo_box {
    position: relative;
    padding-top: 100%;
    transform: translateY(calc(50% + 1rem));
    img {
      position: absolute;
      left: 10%;
      height: 80%;
      width: 80%;
      top: 10%;
      object-fit: contain;
      z-index: 10;
    }
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: #2c3339;
      opacity: 0.82;
      border-radius: 10px;
    }
  }
}

.cTabLine {
  // position: relative;
  .pTab::after {
    display: none;
  }
  &:after {
    content: "";
    position: absolute;
    left: 1rem;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: var(--brand-border);
  }
}

// .map_box {
//   border: 1px solid #d5d5d5;
// }

.closer_section {
  padding-inline-end: 0;
  .off_ {
    padding-inline-end: 1rem;
  }
}
.closerWrap {
  padding-inline-end: 10vw;
  overflow: hidden;
  .swiper,
  .swiper-container {
    overflow: visible;
  }
  .swiper-slide,
  ion-slide {
    height: auto;
  }
}

.close_item {
  height: 100%;
  width: 100%;
  .inner_ {
    padding: 1rem 1rem;
    background-color: var(--color-white);
    border-radius: 0 0 2rem;
    text-align: left;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    height: 100%;
  }
  .t_Box {
    display: grid;
    align-items: flex-end;
    grid-template-columns: 50% 1fr;
    grid-gap: 0.5rem;
    width: 100%;
    margin-bottom: 0.8rem;
  }
  .timeBox {
    display: flex;
    align-items: center;
    font-size: 0.95rem;
    i {
      font-style: normal;
      margin-inline-start: 0.3rem;
      color: var(--brand-text-light);
    }
  }
  .img_box {
    display: flex;
    padding-top: 80%;
    position: relative;
    img {
      object-fit: contain;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }
  .text_box p {
    line-height: 1.3;
  }
}

.materialsWrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0.5rem;

  .inner_ {
    color: var(--brand-text-dark);
    padding: 0.5rem;
    border-radius: 10px;
    background-color: var(--color-white);
    display: grid;
    grid-template-columns: 20px 1fr;
    align-items: center;
    grid-gap: 0.6rem;
    font-size: 0.9rem;
    line-height: 1.2;
  }
  .iconBox {
    display: flex;
    align-items: center;
    img {
      width: 100%;
      height: 25px;
      object-fit: contain;
    }
  }
}

.exSliderWrap {
  padding-inline-end: 15vw;
  overflow: hidden;
  .swiper,
  .swiper-container {
    overflow: visible;
  }
  .swiper-slide,
  ion-slide {
    height: auto;
  }
}

.sliderG_item {
  a {
    display: flex;
    position: relative;
    border-radius: 15px;
    overflow: hidden;
  }
}

.inline_paddEnd {
  padding-inline-end: 1rem;
}

.amenitiesWrap {
  padding-inline-end: 10vw;
  overflow: hidden;
  .swiper,
  .swiper-container {
    overflow: visible;
  }
  .swiper-slide,
  ion-slide {
    height: auto;
  }
}

.ament_item {
  width: 100%;
  height: 100%;
  .inner_ {
    padding: 1rem 0.5rem;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    text-align: center;
    background-color: var(--color-white);
    height: 100%;
  }
  .img_box {
    margin-bottom: 0.5rem;
    img {
      width: 40px;
      height: 40px;
      object-fit: contain;
    }
  }
  .text_box {
    width: 100%;
    p {
      line-height: 1.2;
      font-size: 0.85rem;
    }
  }
}

.unitTypeWrap {
  .img_box {
    border-radius: 15px;
    overflow: hidden;
    display: flex;
    background: #dfdfdf;
    img {
      width: 100%;
      height: auto;
    }
  }
}

.more_wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  .more {
    &.w_icon {
      display: grid;
      grid-template-columns: 20px 1fr;
      grid-gap: 0.5rem;
      align-items: center;
      justify-content: center;
      .icon_box {
        display: flex;
        align-items: center;
        img,
        svg {
          width: 100%;
          height: auto;
        }
      }
    }
  }
}

.constructionWrapRelated {
  .conse_item {
    width: 100%;
    height: 100%;
    display: flex;
    padding-bottom: 3rem;
    .inner_ {
      display: flex;
      position: relative;
      width: 100%;
    }

    .image_box {
      position: relative;
      width: 100%;
      padding-top: 74%;
      border-radius: 15px;
      overflow: hidden;
      background-color: #dfdfdf;
      // &:after {
      //   content: "";
      //   position: absolute;
      //   left: 0;
      //   bottom: 0;
      //   width: 100%;
      //   height: 60%;
      //   background: linear-gradient(
      //     180deg,
      //     rgba(0, 0, 0, 0) 0%,
      //     rgba(0, 0, 0, 1) 100%
      //   );
      //   opacity: 0.7;
      // }
      img {
        width: 100%;
        height: 100%;
      }
    }
    .progressBox {
      position: absolute;
      display: flex;
      left: 1rem;
      width: 30%;
      color: var(--color-white);
      bottom: 0;
      transform: translateY(3rem);
      svg {
        transform: rotate(-90deg);
        width: 100%;
        height: auto;
      }
      .progressBar {
        fill: rgba(#000, 0.6);
        stroke-width: 0.9;
        stroke: var(--brand-color);
        stroke-dasharray: 100 100;
        stroke-dashoffset: 100;
        stroke-linecap: round;
      }
      .c_ {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
      }
      .persBox {
        z-index: 10;
        font-size: 1.5rem;
        line-height: 1;
        font-weight: 500;
      }
      .month_ {
        font-size: 0.95rem;
      }
    }
  }
}

// Arabic
[dir="rtl"] {
  .close_item .inner_ {
    border-radius: 0 0 0 2rem;
    text-align: right;
  }
  .constructionWrapRelated .conse_item .progressBox {
    right: 1rem;
    left: auto;
  }
  .cTabLine:after {
    right: 1rem;
    left: auto;
  }
}

body.dark {
  .unitTypeWrap {
    .img_box {
      background-color: #22272b;
    }
  }
  .constructionWrapRelated .conse_item {
    .image_box {
      background-color: #22272b;
    }
    .progressBox {
      //color: var(--color-black);
      .progressBar {
        fill: rgba(#fff, 0.6);
      }
    }
  }
}
