.big_avatar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 5vh;
  .img_box {
    position: relative;
    overflow: hidden;
    border-radius: 2.5rem;
    padding-top: 50vw;
    width: 50vw;
    box-shadow: 8px 6px 20px #afafaf;
    background-color: #dfdfdf;
    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .change_img {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 35%;
      height: 30%;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 10;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: var(--color-black);
        opacity: 0.6;
        border-radius: 2rem 0;
      }
      svg {
        width: 40%;
        height: auto;
        position: relative;
        z-index: 5;
        stroke: var(--color-white);
      }
    }
  }
  .name_ {
    margin-top: 1rem;
    font-size: 1.2rem;
    line-height: 1.2;
    font-weight: 500;
    letter-spacing: 1px;
    text-align: center;
    padding: 0 1rem;
  }
}

// Arabic
[dir="rtl"] {
  .big_avatar .img_box .change_img {
    left: 0;
    right: auto;
    &::before {
      border-radius: 0 2rem;
    }
  }
}

body.dark {
  .big_avatar .img_box {
    box-shadow: 8px 6px 20px #22272b;
    background-color: #22272b;
  }
