.filterGrid {
  ion-item.input_item {
    ion-select {
      max-width: calc(50vw - 1.5rem);
      min-width: calc(50vw - 1.5rem);
    }
    &.full_ {
      ion-select {
        max-width: calc(97vw - 1.5rem);
        min-width: calc(97vw - 1.5rem);
      }
    }
  }
}

.constructionWrap {
  display: grid;
  grid-gap: 1rem;
}
