.map_wrap {
  display: flex;
  padding: 0;
  min-height: 250px;
  img {
    width: 100%;
    height: auto;
  }
}

.locationInfoSection {
  position: relative;
  overflow: hidden;
  padding-top: 0;
  margin-top: -1.5rem;
  .locItem {
    display: grid;
    grid-gap: 1.5rem;
    padding: 2rem 1rem;
    background-color: var(--color-white);
    border-radius: 2rem 0 0;
    padding-inline-start: 2rem;
    .r_ {
      display: grid;
      grid-template-columns: 22px 1fr;
      grid-gap: 1.5rem;
      align-items: flex-start;
    }
    .icon_box {
      display: flex;

      padding-top: 0.3rem;
      svg {
        width: 100%;
        height: auto;
        max-height: 22px;
      }
    }
    .v_ {
      font-size: 0.95rem;
      font-weight: 400;
      line-height: 1.8;
      a {
        color: var(--barnd-text);
        font-weight: 600;
      }
    }
  }
}

.customerSupportSection {
  display: grid;
  grid-gap: 0.5rem;
  .cSupportItem {
    .inner_ {
      display: grid;
      grid-template-columns: 32px 1fr;
      align-items: center;

      grid-gap: 1rem;

      padding: 1.2rem 1rem;
      border-radius: 15px;
      color: var(--barnd-text);
      position: relative;
      overflow: hidden;
      font-size: 1rem;
      font-weight: 500;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: var(--color-white);
        opacity: 0.67;
        z-index: -1;
      }
    }
    .icon_ {
      display: flex;
      svg {
        width: 100%;
        height: auto;
        max-height: 30px;
        fill: #2c3339;
      }
    }
  }
}

// Arabic
[dir="rtl"] {
  .locationInfoSection .locItem {
    border-radius: 0 2rem 0 0;
  }
}

body.dark {
  .customerSupportSection .cSupportItem .icon_ svg {
    fill: #959595;
  }
}
