.pro_iteam {
  .inner_ {
    position: relative;
    display: grid;
    grid-gap: 0.3rem;
    color: var(--barnd-text);
    text-align: center;
  }
  .image_box {
    display: flex;
    position: relative;
    padding-top: 130%;
    height: 0;
    border-radius: 15px;
    overflow: hidden;
    background-color: #dfdfdf;
  }
  .title_ {
    font-weight: 500;
    line-height: 1;
    margin-top: 0.4rem;
  }
  .location_ {
    font-size: 0.8rem;
    line-height: 1.1;
    color: var(--brand-text-light);
  }
}

body.dark {
  .pro_iteam {
    .inner_ {
      .image_box {
        background-color: #22272b;
      }
    }
  }
}
